<template>
  <div class="cms-list-layout">
    <cms-prompt>
      <li>显示系统使用的广告版位</li>
      <li>可对版位内的广告进行管理</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div class="query-group">
        <label>版位名称</label>
        <el-input v-model="queryParams.positionName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>版位标识</label>
        <el-input v-model="queryParams.positionCode" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
      <section class="cms-batch">
        <cms-button icon="add" type="success" size="small" v-prem="'/advert/position/save'"
         @click="$routerLink('/advert/position/save','save')">新增
        </cms-button>
      </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" ref='dataTable'>
      <el-table-column prop="id" label="id" width="80"></el-table-column>
      <el-table-column prop="positionName" label="版位名称">
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.positionName">{{scope.row.positionName}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="positionCode" label="版位标识" width="260">
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.positionCode">{{scope.row.positionCode}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="discountAmount" label="是否启用" width="100">
        <template slot-scope="scope">
          <cms-switch v-model="scope.row.isEnable" :id="scope.row.id" @change="toggleVisable"></cms-switch>
        </template>
      </el-table-column>
      <el-table-column prop="remark" label="说明">
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.remark">{{scope.row.remark}}</div>
        </template>
      </el-table-column>
      <el-table-column label="操作选项" width='240' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button icon='edit' size='small' type='info'  v-prem="'/advert/list'"
            @click="$routerLink('/advert/list','list',{id:scope.row.id,name:scope.row.positionName})">
            广告管理
          </cms-button>
          <cms-button icon='edit' size='small' type='info'  v-prem="'/advert/position/update'"
          @click="$routerLink('/advert/position/update','update',{id:scope.row.id})"
          >修改
          </cms-button>
      
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import {
  advertPositionPage,
  advertPositionEnable,
  advertPositionDisable
} from '@/api/marketing/index.js'
import list from '@/mixins/list'
var defaultData = {
  positionName: '',
  positionCode: '',
  page: 1,
  size: 10
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    toggleVisable(status, id) {
      if (status) {
        advertPositionEnable({ id: id }).then(res => {
          if (res.code !== 200) {
            this.$errorMessage('操作失败')
          }
        })
      } else {
        advertPositionDisable({ id: id }).then(res => {
          if (res.code !== 200) {
            this.$errorMessage('操作失败')
          }
        })
      }
    }
  },
  created() {
    this.getTableData(advertPositionPage, this.queryParams)
  }
}
</script>

<style>
</style>



